
.Items{
  float: right;
  background-color: "red";
}



.App-logo {
  height: 6vmin;
  pointer-events: none;
  float: left;
  margin: left 20px;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #ffffff;
  min-height: 5vh;
  display: flex;
  margin-top: 1%;
  margin-left: 3%;
  margin-right: 3%;
  flex-direction: row;
  justify-content: space-between;
  font-size: calc(5px + 2vmin);
  color: rgb(34, 33, 33);
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}


* {
  margin: 0;
  padding: 0;
  font-family: "poppins", sans-serif;
  box-sizing: border-box;
}
.underline {
  width: 20%;
  height: 3px;
  padding-bottom: 0%;
  margin-bottom: 0%;
  background-color: rgb(0, 255, 136);
}
.logouticon {
  font-size: 20px;
}
.heading-s {
  padding-top: 1%;

}
.hero {
  background-color: #ffffff;
  min-height: 100vh;
  width: 100%;
  color: rgb(10, 9, 9);
  position: relative;
}
/* nav bar designing.....*/
.nav {
  display: flex;
  align-items: center;
  padding: 20px 8%;
}
.log-btn {
  width: 200px;
}
.logo {
  width: 160px;
  cursor: pointer;
}
ul {
  flex: 1;
  text-align: right;
  width: 100%;
}
li {
  display: inline-block;
  list-style: none;
  margin: 0 20px;
  text-decoration: none;
}
a {
  color: rgb(47, 46, 46);
  width: 200px;
  padding: 6px;
  border-radius: 20px;
  font-size: 16px;
  text-decoration: none;
  padding: 10px;
}

/*about section */
